@import url(https://fonts.googleapis.com/css2?family=Fredoka+One&family=Montserrat&display=swap);
nav{width:100%;height:10vh;display:flex;justify-content:center;align-items:center}nav #logo{margin:0 auto 0 20px;font-family:'Fredoka One', cursive}nav #logo .logoImg{width:150px;height:60px}nav a{color:black}nav #nav-links #nav-button a{color:white}#nav-links li{display:inline-block;margin:0 20px}#burger{display:none;cursor:pointer;position:absolute;top:9vh;right:5vh;transform:translate(-5%, -50%)}#burger div{width:25px;height:3px;background-color:black;margin:5px;transition:all 0.3s ease}.fixed-position{overflow:hidden}.toggle .line1{transform:rotate(-45deg) translate(-5px, 6px)}.toggle .line2{opacity:0}.toggle .line3{transform:rotate(45deg) translate(-5px, -6px)}

#about{width:100%}#about h1{margin:0}#about-back{position:absolute;background-image:radial-gradient(at center center, #153243 0%, #1a6c7a 100%);opacity:0.5;background-color:transparent;width:100%;height:100%;z-index:-1}#about-head{color:white;text-align:center;position:relative;background-image:url(/static/media/idea.241f6c50.png);background-repeat:no-repeat;background-size:cover;background-position:bottom center;width:100%;height:100%;z-index:-1}#about-head h1{font-size:60px;padding:150px}#about-info{margin:40px;display:flex;justify-content:space-evenly;align-items:center}#about-info #about-info-head{border-right:2px solid grey}#about-info #about-info-head h1{font-size:70px;color:grey}#about-info #about-info-para{margin-left:30px}#about-info #about-info-para p{font-size:20px;color:#464646}#about-missions{display:flex;justify-content:space-evenly;align-items:center;background-color:#f3f9fb}#about-missions .mission{margin:70px;padding:40px}#about-missions .mission h3{text-align:center;font-size:26px;margin:0}

.button{width:200px;padding:15px;text-align:center;transition:all 0.3s ease;box-shadow:none}p{margin:0;text-decoration:none;font-size:18px;display:inline-block}#transparent-button{background:transparent;border:2px solid white}#solid-button{color:#1a6c7a;background:white;margin:10px}#solid-button p{color:#1a6c7a}#transparent-button:hover{background:#1a6c7a;border:2px solid #1a6c7a}#transparent-button:hover p{color:white}#solid-button:hover{background:#1a6c7a}#solid-button:hover p{color:white}#nav-button{background:#1a6c7a;margin:0 20px;width:120px}#nav-button p{color:white;font-size:15px}#nav-button:hover{background:#269fb4}

#card{height:350px;width:300px;display:flex;margin:20px;flex-direction:column;justify-content:space-between;align-items:center;background-color:white}#card .card-img{height:200px;width:100%;background-size:cover;background-repeat:no-repeat;background-position:center}#card #title{text-align:center;color:black}#card #title h3{margin:0}

#home{width:100%;height:90vh;display:flex;flex-direction:column;justify-content:center;align-items:center;background-position:center;text-align:center;background-size:cover;background-image:url("https://images.freeimages.com/images/large-previews/5ae/rice-fields-1564839.jpg");color:#494949;z-index:5}.button-holder{margin:100px;height:100%;width:50%;display:flex;justify-content:space-evenly;align-items:center}#home-content{background-color:transparent;width:100%;height:100%;z-index:-1}#home-contact{width:100%;height:100vh;display:flex;position:relative;flex-direction:column;justify-content:space-evenly;text-align:center;align-items:center;background-size:cover;background-repeat:no-repeat;background-position:center center;background-attachment:scroll;background-image:url(/static/media/farmers.fa09757a.jpg);color:white;z-index:5}#home-contact h1{font-size:50px;margin:100px 0}.button-holder{width:50%;margin-bottom:200px;display:flex;justify-content:space-evenly;align-items:center}#home-contact-content{position:absolute;background-image:radial-gradient(at center center, #153243 0%, #1a6c7a 100%);opacity:0.5;background-color:transparent;width:100%;height:100%;z-index:-1}#announcements{width:100%;min-height:100vh;padding:50px 0;background-color:#dcd9d4;background-image:linear-gradient(to bottom, rgba(255,255,255,0.5) 0%, rgba(0,0,0,0.5) 100%),radial-gradient(at 50% 0%, rgba(255,255,255,0.1) 0%, rgba(0,0,0,0.5) 50%);background-blend-mode:soft-light, screen}#announcements #announcement-cards{display:flex;justify-content:space-evenly;align-items:center;padding-bottom:10px;border-bottom:1px solid black}#announcement-content{display:flex;justify-content:space-evenly;align-items:flex-start;text-align:center}#announcement-content h3{font-size:40px;margin:auto 0;flex:1 1}#announcement-content .para{border-left:3px solid #1a6c7a;flex:1 1}#announcement-content .para p{font-size:24px}#announcement-content div{margin:30px auto 0 auto}

#contact{width:100%}#contact h1{margin:0}#contact h2{margin:0;font-size:40px}#contact h3{margin:0;font-size:30px}#contact #contact-info{margin:20px;display:flex;justify-content:space-evenly}#contact #contact-info p{display:block}#contact #contact-info #get-in-touch{display:flex;flex-direction:column;justify-content:space-evenly}#contact #contact-form{padding:20px}#contact #contact-form form input{display:block;font-size:20px;width:100%;border:1px solid #eaeaea;margin:40px 15px;padding:5px;transition:all 0.3s ease}#contact #contact-form form input:focus{border:1px solid #1a6c7a}#contact #contact-form form #solid-button{margin:0 auto}#contact #contact-form #contact-btn{width:200px;padding:15px;text-align:center;transition:all 0.3s ease;font-size:18px;box-shadow:none;color:#1a6c7a;background-color:white}#contact #contact-form #contact-btn:hover{background:#1a6c7a;border:2px solid #1a6c7a;color:white}#contact-back{position:absolute;background-image:radial-gradient(at center center, #153243 0%, #1a6c7a 100%);opacity:0.5;background-color:transparent;width:100%;height:100%;z-index:-1}#contact-head{color:white;text-align:center;position:relative;background-image:url(/static/media/phone.68e1a059.jpg);background-repeat:no-repeat;background-size:cover;background-position:bottom center;width:100%;height:100%;z-index:-1}#contact-head h1{font-size:60px;padding:150px}#map{overflow:hidden;height:500px;margin:20px 0 0 50px}

#services{width:100%}#services h1{margin:0}#services h2{margin:0;font-size:40px}#services h3{margin:0;font-size:30px}#services #services-head{color:white;text-align:center;position:relative;background-image:url(/static/media/handshake.7789e581.png);background-repeat:no-repeat;background-size:cover;background-position:center;width:100%;height:100%;z-index:-1}#services #services-head h1{font-size:60px;padding:150px}#services #services-back{position:absolute;background-image:radial-gradient(at center center, #153243 0%, #1a6c7a 100%);opacity:0.5;background-color:transparent;width:100%;height:100%;z-index:-1}#services #services-info .service{display:flex;justify-content:space-evenly;align-items:center}#services #services-info .service .text{margin:0 70px}#services #services-info .service .text h3,#services #services-info .service .text p{margin:10px 0}#services #services-info .service .text .button{margin:70px 0 0}#services #services-info .service .text .button a{color:#1a6c7a}#services #services-info .service .text .button:hover a{color:white}#services #services-info .service .img{margin:50px 70px;background-size:cover;background-position:center;height:400px;width:100%}

#commodities{width:100%}#commodities #commodities-head{color:white;text-align:center;position:relative;background-image:url(/static/media/flea-market.50d92d02.jpg);background-repeat:no-repeat;background-size:cover;background-position:center;width:100%;height:100%;z-index:-1}#commodities #commodities-head h1{font-size:60px;padding:150px}#commodities #commodities-head #commodities-back{position:absolute;background-image:radial-gradient(at center center, #153243 0%, #1a6c7a 100%);opacity:0.5;background-color:transparent;width:100%;height:100%;z-index:-1}#commodities h1,#commodities h2,#commodities h3{margin:0}#commodities h2{font-size:40px}#commodities h3{font-size:30px}#commodities #commodities-info{text-align:center;width:100%;margin:20px 0;display:flex;flex-direction:column;align-items:center}#commodities #commodities-info h1{font-size:45px;margin:50px 0}#commodities #commodities-info #filters{width:100%;margin:25px}#commodities #commodities-info #filters input{margin-left:20px}#commodities #commodities-info #table{display:flex;flex-direction:column;text-align:center;width:90%;border:1px solid grey}#commodities #commodities-info #table #table-head{display:flex;justify-content:space-evenly;margin:10px}#commodities #commodities-info #table #table-head h2{font-size:25px;width:20%;margin:10px}#commodities #commodities-info #table #table-body{width:100%}#commodities #commodities-info #table #table-body .table-row{display:flex;justify-content:space-evenly;width:100%;margin:10px 0;border-top:1px solid grey}#commodities #commodities-info #table #table-body .table-row .table-data{width:20%;margin:10px}

footer{min-height:3vh;text-align:center;margin:10px 0;padding:auto}

body{margin:0;font-family:'Montserrat', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;overflow-x:hidden}a{color:white;text-decoration:none}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

@media screen and (max-width: 1024px){#burger{display:inline-block;margin-top:10px}#nav-links{position:fixed;min-height:92vh;width:100%;top:8vh;margin:0;background-color:white;display:flex;flex-direction:column;align-items:center;justify-content:space-around;transition:all 0.5s ease-out;clip-path:circle(100px at 90% -20%);-webkit-clip-path:circle(100px at 100% -20%);pointer-events:none}#nav-links #nav-button{height:30px;width:250px}#nav-links li{font-size:30px}#nav-links.open{top:8vh;min-height:92vh;margin:0;padding:0;clip-path:circle(1500px at 90% -10%);-webkit-clip-path:circle(1500px at 90% -10%);pointer-events:all}#announcement-cards{flex-direction:column}#announcement-content{flex-direction:column;justify-content:center;margin:50px;align-items:center}#announcement-content h3{font-size:40px;flex:1 1}#announcement-content .para{border:none;flex:1 1}#home h2,#home h1{margin:50px 0}#home-contact h1{font-size:50px;margin:100px 0}.button-holder{flex-direction:column;margin-bottom:50px}#about #about-head h1{font-size:40px;padding:150px 20px}#about #about-info{display:flex;flex-direction:column}#about #about-info #about-info-head{border:0}#about #about-info #about-info-head h1{font-size:25px;font-weight:700;padding:10px 0}#about #about-info #about-info-para{margin:8px;display:flex;flex-direction:column;justify-content:space-evenly}#about #about-info #about-info-para P{width:100%}#about #about-missions{display:flex;flex-direction:column;justify-content:space-evenly;align-items:center}#about #about-missions .mission{padding:0;margin:50px}#contact #contact-head h1{font-size:40px;padding:150px 50px}#contact #contact-info{display:flex;flex-direction:column}#contact #contact-info #get-in-touch{padding-bottom:35px;border-bottom:1px solid black}#contact #contact-info #get-in-touch div{margin:10px}#contact #contact-info h2{margin:20px 0}#contact #contact-info h3{font-size:18px}#contact #contact-form{padding:10px;border-bottom:1px solid black}#contact #contact-form form input{margin:40px 0;width:80%}#contact #map{margin:20px 0 0 10px}#services #services-head h1{font-size:40px;padding:150px 50px}#services #services-info .service{flex-direction:column;border-bottom:1px solid black}#services #services-info .service .img{height:250px;width:85%}#services #services-info .service:last-child{border:none}#commodities #commodities-head h1{font-size:40px;padding:150px 50px}#commodities #commodities-info #table #table-head h2{font-size:17px}#commodities #commodities-info #table #table-body .table-row .table-data{font-size:15px;margin:10px 2px}}@media screen and (max-width: 500px){#home h2,#home h1{margin:15px 0}#map{margin:20px 0 0 10px}}

